import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';
import { MyServiceService } from 'src/app/my-service.service';
import { RouterModule } from '@angular/router';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  constructor(private router:Router,private service:MyServiceService) { }
  listData;
  items=[];
  tables=[];
  lname;
  ngOnInit(): void {
    console.log("hello list");
    var list = localStorage.getItem("list");
    console.log(list);
    if(list!=null){
      this.items=JSON.parse(list);
    }
    console.log(this.items);
    this.listData=new FormGroup({
      name:new FormControl(''),
      quantity:new FormControl(''),
      listname:new FormControl('')
    })
  }
  OnClickLogout(){
    localStorage.removeItem("currentUser");
    console.log("hello logout");
    this.router.navigate(['']);
    console.log("hello logout1");
  }
  addNewList(){
    console.log("Inside the method addNewList");
    this.router.navigate(['grlist']);
  }
  addItem(listname){
    console.log("Inside method addItem- "+listname);
    if(listname){
      this.items.push({listname});
      console.log(this.items);
      this.lname=listname;
      console.log("Inside if condition- "+listname);
      this.listData.controls['listname'].reset();
    }
  }
  deleteItem(i){
    console.log("Inside method of deleteItem");
    this.items.splice(i,1);
    var bookmarks = JSON.parse(localStorage.getItem('list'))
    bookmarks.splice(i, 1)
    console.log("local data"+ i + bookmarks);
    localStorage.setItem('list', JSON.stringify(bookmarks));
  }
  OnClickFinish(){
    console.log("Inside method OnClickFinish");
    localStorage.setItem("list",JSON.stringify(this.items));
    console.log("finish list:  " +this.items);
    this.listData.reset();
  }
  onClickList(listname){
    console.log("Inside the method OnClickList");
    this.router.navigate(['/grlist',listname]);
  }
}
